<template>
  <div class="tabfont">
      <!-- <div class="yidong" :style="{ transform: translatex, width: ass }"></div>-->
    
    <span
      @click="current(index)"
      :class="{ reds: index == curr }"
      v-for="(item, index) in tabxingxi"
      :key="index"
    >
      {{ item }}
       </span>
  </div>
</template>
<script>
export default {
  name: "Tabfont",
  props: {
    tabxingxi: Array,
   
  },
  data() {
    return {
      translatex: "0",
     // ass: "",
      curr: 0,
      
     
    };
  },
  
 //mounted() {
//let xx = (this.curr+1)*(this.$refs.element.offsetWidth / this.tabxingxi.length);
    //this.ass = "" + xx + "px";
    
 // },
  methods: {
    current(index) {
             this.curr=index
              this.$emit("itemclick", index); //组件通信子传父，传过去一个参数index
     
     
    },
  },
};
</script>
<style scoped>
.tabfont {
  display: flex;
  width: 100%;
  position: relative;
  text-align: center;
  /*  position: sticky;
        top:40px;这个有时候不兼容 */
  background-color: white;
  height: 40px;
 
}
.tabfont span {
  flex: 1;
  padding-top: 12.5px;
 margin:0 30px;
}
.reds {
  color: red;
  border-bottom:2px  solid red;
}


</style>