<template>
  <div id="home" class="animate__animated animate__fadeIn">
    <naver>
    <p slot="center"> 购物街</p>
    </naver>
    <!--这里的content不影响封装后的组件中的content,前提是style中设有scoped-->
    <!--可以直接在这里绑定子传父的通信-->
    <!--也可以直接在组件这里动态绑定class-->

    <tabfont
      ref="q2"
      class="guding"
      v-show="fdfd"
      @itemclick="tabfont"
      :tabxingxi="['流行', '新款', '精选']"
    ></tabfont>
    <!--注意，给子组件传值时，不加：则传的是字符串类型，-->
    <scroll @sscroll="yingcang" class="content" ref="scroll" 
     @pullingUp="pulling"><!--@pulling-up="pulling"-->
      <shouyeswiper :tupian="tutu"></shouyeswiper>
      <div class="zhongjian">
        <div v-for="(item,index) in recommends" :key="index" class="xuexi">
          <!--必须加key-->
          <a :href="item.link">
            <!--必须加:-->
            <img :src="item.image" />
            <!--必须加:-->
            <div>{{ item.title }}</div>
          </a>
        </div>
      </div>
      <!---->
      <tabfont
        ref="q1"
        @itemclick="tabfont"
        :tabxingxi="['流行', '新款', '精选']"
      ></tabfont>
      <gooditem :goods="goods[currentType].list" @sde="ser"></gooditem>
    </scroll>
    <!--这里可以直接写v-show-->
        <transition name="vf">
    <topwid @click.native="runtop" v-show="isshow"></topwid
    ><!--直接在组件中给事件需要加.native-->
    </transition>
  </div>
</template>
<script>
import Naver from "../../components/common/naver/Naver.vue";
import Shouyeswiper from "./shouyeitem/Shouyeswiper.vue";
import Tabfont from "../../components/content/Tabfont.vue";
import Gooditem from "../../components/content/goodss/Gooditem.vue";
import Topwid from "../../components/content/Topwid.vue";
import Scroll from "../../components/content/Scroll.vue";
import {debounce} from "../../components/common/utils.js"
import axios from "axios";
import { getHome, getHomeGoods } from "../../network/shouyess"; //引用首页数据中的方法
export default {
  name: "Shouyess",
  components: {
    Naver,
    Shouyeswiper,
    Tabfont,
    Gooditem,
    Topwid,
    Scroll,
  },
  data() {
    return {
      recommends: [],
      tutu: [],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop",
      isshow: false,
      fdfd: false,
      timer:null,
      aa:null,
      aw:null,
    };
  },
  created() {
    //首页轮播图数据和zhongjianjian数据处理
    getHome().then((res) => {
     // console.log(res)
      this.recommends = res.data.data.recommend.list;
       this.tutu = res.data.data.banner.list; 
    });
    //调用methods中封装的方法来请求图片数据
    this.getHomeGoods("pop");
    this.getHomeGoods("new");
    this.getHomeGoods("sell");
  },
  mounted(){
    this.aw=debounce(this.$refs.scroll.refresh,500)
    this.qrr={}
console.log(this)
    },
  methods: {
    //图片加载完再计算高度
ser() {
this.aw()//注意：在methods中使用防抖需要将函数定义在data中，如果是ref,可以在mounted中修改
 //this.$refs.scroll.refresh();//注意，refs不能在created里用，最好在mounted里面做
     },
    //图片数据请求处理方法
    getHomeGoods(type) {
      const page = this.goods[type].page + 1; //注意，这里的page是临时变量调用完会得销毁掉，
      getHomeGoods(type, page).then((res) => {
        //这里不能直接将res.data.data赋值给this.goods[type].list,否则下一次再赋值的时候，之前的数组会被覆盖掉，用push不会覆盖掉之前添加的数组
        this.goods[type].list.push(...res.data.data.list);
        this.goods[type].page += 1; //因为定义的page是临时变量，因此需要自增1
      });
    },
    //上拉加载更多事件
     pulling(){
         this.getHomeGoods(this.currentType)
         //this.$refs.scroll.refresh()
      },
    //tabfont点击事件处理
    tabfont(index) {
      //通信子传父，参数index
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      if (this.$refs.scroll.posit <= -this.$refs.q1.$el.offsetTop) {
        this.$refs.scroll.scroll.scrollTo(0, -this.$refs.q1.$el.offsetTop);
      }
      this.$refs.q1.curr = index;
      this.$refs.q2.curr = index;
    },
    //Topwid点击回到顶部事件处理
    runtop() {
      this.$refs.scroll.scroll.scrollTo(0, 0, 500); //better-scroll自带的方法，第三个参数相当于过度
    },
    //Topwid按钮v-show事件处理
    yingcang(position) {
     //console.log(this.$refs.q1.$el.offsetTop)
      if (position.y<-1000) {
        this.isshow = true;
      } else {
        this.isshow = false;
      }
      if (-position.y > this.$refs.q1.$el.offsetTop) {
        this.fdfd = true;
      } else {
        this.fdfd = false;
      }
    },
  },
};
</script>
<style scoped>
.vf-enter{
  opacity: 0;
  transform: translateY(50px);
}
.vf-enter-to{
  opacity: 1;
}
.vf-enter-active{
    transition: all 0.6s;
}
.vf-leave-to{
 opacity:0;
 transform: translateY(50px);
}
.vf-leave-active{
    transition: all 0.6s;
}
#home {
  background-color: white;
  height: 100vh;
  /*vh,浏览器单位*/
}

.naver {
  background-color: rgb(255, 94, 0);
  font-size: 20px;
  color: white;
}

.zhongjian {
  background-color: white;
  width: 100%;
  height: 100px;
  text-align: center;
  font-size:13px;
}

.zhongjian img {
  width: 70%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.xuexi {
  position: relative;
  width: 25%;
  height: 100%;
  float: left;
  padding-top: 10px;
}

.xuexi div {
  margin-bottom: 4px;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.content {
  position: fixed; /*better-scroll插件最好加一个固定定位*/
  top: 44px;
  width: 100%;/*注意，better-scroll插件一定要给宽度！！ */
  height: calc(100% - 96px);
  /*减号前面后面跟空格，切记切记，父亲宽度减去购物街和切换蓝高度*/
  overflow: hidden;
  /*也可以用定位*/

  /*减去购物街高度*/
}
.guding {
  position: fixed;
  top: 44px;
  z-index: 99999;
}
</style>